<template>
    <div class="hello">

        <el-table :data="tableData" border class="m-t-25">
            <el-table-column prop="1" label="公司名称"></el-table-column>
            <el-table-column prop="2" label="手机号"></el-table-column>
            <el-table-column prop="3" label="注册用户名"></el-table-column>
            <el-table-column prop="4" label="注册品牌"></el-table-column>
            <el-table-column prop="5" label="起始品类"></el-table-column>
            <el-table-column prop="6" label="提交时间"></el-table-column>
            <el-table-column prop="7" label="操作" width="80">
                <template scope="scope">
                    <el-button @click="handleFn(scope.$index, tableData)" type="text" size="small">审核</el-button>
                </template>
            </el-table-column>
        </el-table>

        <h1>{{ msg }}</h1>
        <h2>Essential Links</h2>
        <ul>
            <li>
                <a href="https://vuejs.org" target="_blank">Core Docs</a>
            </li>
            <li>
                <a href="https://forum.vuejs.org" target="_blank">Forum</a>
            </li>
            <li>
                <a href="https://gitter.im/vuejs/vue" target="_blank">Gitter Chat</a>
            </li>
            <li>
                <a href="https://twitter.com/vuejs" target="_blank">Twitter</a>
            </li>
            <br>
            <li>
                <a href="http://vuejs-templates.github.io/webpack/" target="_blank">Docs for This Template</a>
            </li>
        </ul>
        <h2>Ecosystem</h2>
        <ul>
            <li>
                <a href="http://router.vuejs.org/" target="_blank">vue-router</a>
            </li>
            <li>
                <a href="http://vuex.vuejs.org/" target="_blank">vuex</a>
            </li>
            <li>
                <a href="http://vue-loader.vuejs.org/" target="_blank">vue-loader</a>
            </li>
            <li>
                <a href="https://github.com/vuejs/awesome-vue" target="_blank">awesome-vue</a>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: 'hello',
    data() {
        return {
            msg: 'Welcome to Your Vue.js App',
            tableData: [{
                1: '广州拓恪汽车部件有限公司',
                2: '13570336092',
                3: 'TT',
                4: 'TATK',
                5: '刹车片',
                6: '2017-07-10 18:43:05'
            }, {
                1: '广州拓恪汽车部件有限公司',
                2: '13570336092',
                3: 'TT',
                4: 'TATK',
                5: '刹车片',
                6: '2017-07-10 18:43:05'
            }]
        };
    },
    methods: {
        // 操作方法
        handleFn(index, rows) {
            this.$confirm('通过当前商户的进驻申请?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'info'
            }).then(() => {
                rows.splice(index, 1);
                this.$notify.info({
                    title: '消息',
                    message: '用户审核成功',
                    duration: 1000
                });
            });
        }
    }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1,
h2 {
    font-weight: normal;
}

ul {
    list-style-type: none;
    padding: 0;
}

li {
    display: inline-block;
    margin: 0 10px;
}

a {
    color: #42b983;
}
</style>



