// 设定title
function returnTitle(title) {
    return title;
}

// 验证空对象{}
function isEmptyObject(o) {
    var t;
    for (t in o)
        return !1;
    return !0;
}

// talkingdata数据上报
function onEvent(keyword, Label) {
    try {
        if (Label) {
            TDAPP.onEvent(keyword, Label);
        } else {
            TDAPP.onEvent(keyword);
        }
    } catch (error) {
        console.log(error);
    }
}

// 表单验证-手机号码验证
function validateTel(rule, value, callback) {
    let regExp = /^(13[0-9]|15[012356789]|17[678]|18[0-9]|14[57])[0-9]{8}$/;
    if (value === '') {
        callback(new Error('手机号码不能为空'));
    } else if (regExp.test(value) === false) {
        callback(new Error('请正确输入11位的手机号码'));
    } else {
        callback();
    }
}

export default {
    returnTitle: returnTitle,
    isEmptyObject: isEmptyObject,
    validateTel: validateTel,
    onEvent: onEvent
};




