<template>
    <div v-title data-title="用户管理-开通新品类-查看详情">
        <div class="userCategoryDetail p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/userCategoryList' }">新品类开通</el-breadcrumb-item>
                <el-breadcrumb-item>查看详情</el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row>
                <el-col :span="14" :offset="4">
                    <div class="title text-center m-b-25">品类详情</div>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">

                        <el-form-item label="开通公司" prop="company">
                            <el-select v-model="ruleForm.company" filterable placeholder="请选择" class="input-width" disabled>
                                <el-option v-for="item in companyOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="使用品牌" prop="brand">
                            <el-select v-model="ruleForm.brand" filterable placeholder="请选择" class="input-width" disabled>
                                <el-option v-for="item in brandOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="开通品类" prop="category">
                            <el-select v-model="ruleForm.category" filterable placeholder="请选择" class="input-width" disabled>
                                <el-option v-for="item in categoryOptions" :key="item.value" :label="item.label" :value="item.value">
                                </el-option>
                            </el-select>
                        </el-form-item>

                        <el-form-item label="开通产品" prop="part">
                             <el-card class="card">
                                <el-checkbox-group v-model="ruleForm.part">
                                    <el-checkbox :label="item.label" v-for="item in partOptions" :key="item.value" disabled></el-checkbox>
                                </el-checkbox-group>
                             </el-card>
                        </el-form-item>

                        <el-form-item label="销售价格" prop="price">
                            <el-row>
                                <el-col :span="22">
                                    <el-input type="number" v-model.number="ruleForm.price" disabled></el-input>
                                </el-col>
                                <el-col class="text-center" :span="2">元</el-col>
                            </el-row>
                        </el-form-item>

                        <el-form-item label="有效期">
                            <el-row>
                                <el-col :span="11">
                                    <el-form-item prop="dateStart">
                                        <el-date-picker type="date" placeholder="选择开始日期" v-model="ruleForm.dateStart" class="input-width" disabled></el-date-picker>
                                    </el-form-item>
                                </el-col>
                                <el-col class="text-center" :span="2">-</el-col>
                                <el-col :span="11">
                                    <el-form-item prop="dateEnd">
                                        <el-date-picker type="date" placeholder="选择结束日期" v-model="ruleForm.dateEnd" class="input-width" disabled></el-date-picker>
                                    </el-form-item>
                                </el-col>
                            </el-row>
                        </el-form-item>

                        <el-form-item label="销售人员" prop="salesman">
                            <el-input type="text" v-model="ruleForm.salesman" disabled></el-input>
                        </el-form-item>

                        <el-form-item class="text-center">
                            <el-row :gutter="20">
                                <el-col :span="12" :offset="6">
                                    <el-button class="input-width" type="primary" @click="routeTo('userCategoryList')"> 返回 </el-button>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </el-form>

                </el-col>
            </el-row>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/userCategoryDetail';

export default {
    name: 'userCategoryDetail',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.title {
    font-size: 24px;
    font-weight: bold;
}
.input-width{width: 100%;}
.card{box-shadow:none;}
.card .el-card__body{padding: 10px;}
</style>

<style lang="scss">
.userCategoryDetail .el-card__body{padding: 10px;}
</style>



