import router from '@/router';

export default {
    name: 'catgConfig',
    data() {
        return {
            tableData: [{
                1: '刹车片',
                2: '前刹车片,前感应线,后刹车片',
                3: '2017-07-10 18:43:05',
                4: '2017-07-10 18:43:05',
                5: '已启用'
            }, {
                1: '刹车片',
                2: '前刹车片,前感应线,后刹车片',
                3: '2017-07-10 18:43:05',
                4: '2017-07-10 18:43:05',
                5: '未启用'
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        handleFn(index, rows) {
            if(index == undefined && !rows){
                this.routeTo('catgAdd');
            }else{
                console.log(rows);
                console.log(arguments);
                this.routeTo('catgEdit');
            }

        },
        // 改变每页条数
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        // 分页数据
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        // 跳转页面
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



