<template>

    <el-menu class="page-left-box" :default-active="currentUrl" theme="light">

        <el-submenu :index="menu.id" v-for="menu in menusData" :key="menu.id">
            <template slot="title">
                <i class="iconfont m-r-5" :class="menu.icon"></i>
                {{menu.name}}
            </template>
            <!-- :route="{query:{id:1}}" 可设置参数，对象形式-->
            <el-menu-item :index="sub.url" v-for="sub in menu.subMenu" :key="sub.id" @click="routeTo(sub.url, sub.params, sub.query)">
                {{sub.name}}
            </el-menu-item>
        </el-submenu>

    </el-menu>

</template>

<script>
// import Util from '@/assets/js/utility';
import router from '@/router';

export default {
    name: 'pageLeft',
    data() {
        return {
                currentUrl:this.$route.name,
                menusData: [{
                    id: '0',
                    name: '用户管理',
                    icon: 'icon-shuke_tianchong',
                    subMenu: [{
                        id: '0-1',
                        name: '注册审核',
                        params:{currentView:'reviewed'},
                        url: 'userReview'
                    }, {
                        id: '0-2',
                        name: '新品类开通',
                        url: 'userCategoryList'
                    }]
                }, {
                    id: '1',
                    name: '运营管理',
                    icon: 'icon-releaedp',
                    subMenu: [{
                        id: '1-1',
                        name: '品类配置',
                        url: 'catgConfig'
                    }]
                }]
            };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        routeTo(url, paramsObj, queryObj) {
            router.push({
                name: url,
                params: paramsObj,
                query: queryObj
            });
        }

    }
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";

.page-left-box{
    border-radius: 0;
    background-color: $color-F8;
}
</style>



