<template>
    <div v-title data-title="搜配-后台管理">
        <div class="login-box">
            <span class="login-box-logoBg">
                <span class="iconfont icon-soupei login-logo-large"></span>
            </span>

            <el-form :model="ruleForm" :rules="rules" ref="ruleForm" class="login-form">

                <el-form-item prop="tel" class="login-input">
                    <span class="iconfont icon-phonner login-icon"></span>
                    <el-input placeholder="请输入手机号码" v-model="ruleForm.tel" size="large" min="11" max="11">
                    </el-input>
                </el-form-item>

                <el-form-item prop="pwd" class="login-input">
                    <span class="iconfont icon-passwoad login-icon"></span>
                    <el-input type="password" placeholder="请输入密码" v-model="ruleForm.pwd" size="large" max="20">
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" size="large" @click="submitForm('ruleForm')" class="login-submit">登录</el-button>
                </el-form-item>

            </el-form>

        </div>

    </div>
</template>

<script>

// import { mapGetters } from 'vuex';
// import * as types from '@/vuex/modules/state-types.js';
import router from '@/router';
import Util from '@/assets/js/utility';


export default {
    name: 'login',
    data() {
        return {
            ruleForm: {
                tel: '',
                pwd: ''
            },
            rules: {
                tel: [
                    { validator: Util.validateTel, trigger: 'blur' }
                ],
                pwd: [
                    { required: true, message: '请正确输入密码', trigger: 'blur' },
                    { min: 6, max: 20, message: '长度在 6 到 20 个字符', trigger: 'blur' }
                ]
            }
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    console.log('登录成功');
                    this.routeTo('userReview');

                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },

        // 跳转页面
        routeTo(url) {
            router.push({
                name: url,
                params: {
                    currentView: 'reviewed'
                }
            });
        }
    }
};

</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";

.login-box {
    width: 530px;
    height: 460px;
    margin: -230px auto 0 -265px;
    left: 50%;
    top: 50%;
    position: absolute;
    background-color: $color-F;
    box-shadow: 0 0 30px $color-C;
}

.login-box-logoBg {
    background-color: $color-blue;
    width: 146px;
    height: 146px;
    line-height: 146px;
    border-radius: 50%;
    display: block;
    text-align: center;
    color: $color-F;
    margin: 25px auto 30px auto;
}

.login-logo-large {
    font-size: 135px;
}

.login-form {
    width: 80%;
    margin: 0 auto;
}

.login-icon {
    position: absolute;
    font-size: 16px;
    left: 0;
    top: 1px;
    z-index: 1;
    color: $color-C;
    width: 42px;
    height: 40px;
    text-align: center;
    padding-top: 5px;
    border-right: solid 1px $color-C;
}

.login-submit {
    width: 100%;
}
</style>

<style lang="scss">
@import "../../assets/css/config.scss";
.login-input {

    input.el-input__inner::-webkit-input-placeholder {
        color: $color-C;
    }
    input.el-input__inner {
        padding-left: 50px;
    }
}
</style>



