import router from '@/router';

import reviewed from '@/components/main/userReview/reviewed';
import unReviewed from '@/components/main/userReview/unReviewed';

export default {
    name: 'userReview',
    data() {
        return {
            currentView: this.$route.params.currentView,
            tabs: [{
                btnName: '待审核',
                btnType: '',
                keyWord: 'reviewed'
            },{
                btnName: '已审核',
                btnType: '',
                keyWord: 'unReviewed'
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {
            this.pageInit();
        });
    },
    component: {
        reviewed: reviewed,
        unReviewed: unReviewed
    },
    methods: {
        // 初始化
        pageInit() {
            this.tabs.forEach((val)=> {
                let isCurrentView = val.keyWord == this.currentView;
                if(isCurrentView){
                    val.btnType = 'primary';
                }
            });
        },
        // 切换tab
        tabsFn(tab) {
            // 跳转页面
            router.push({
                name: 'userReview',
                params: {
                    currentView: tab.keyWord
                }
            });
            this.currentViewFn(tab);
        },

        // 更改当前视图状态
        currentViewFn(tab) {
            this.currentView = tab.keyWord;
            this.tabs.forEach((val)=> {
                val.btnType = '';
            });
            tab.btnType = 'primary';
        }
    }

};




