import Vue from 'vue';
import Router from 'vue-router';

import Loading from '@/components/page/loading';
import NotFoundComponent from '@/components/page/notFoundComponent';
import Hello from '@/components/Hello';

import Main from '@/components/page/_MAIN';

import Login from '@/components/page/login';

Vue.use(Router);

export default new Router({
    routes: [
        {
            path: '/',
            name: 'loading',
            component: Loading
        },
        {
            path: '/login',
            name: 'login',
            component: Login
        },
        {
            // 用户管理 - 注册审核
            path: '/userReview/:currentView',
            name: 'userReview',
            component: Main
        },
        {
            // 用户管理 - 新品类开通 - 列表
            path: '/userCategoryList',
            name: 'userCategoryList',
            component: Main
        },
        {
            // 用户管理 - 新品类开通 - 开通新品类
            path: '/userCategoryAdd',
            name: 'userCategoryAdd',
            component: Main
        },
        {
            // 用户管理 - 新品类开通 - 查看详情
            path: '/userCategoryDetail',
            name: 'userCategoryDetail',
            component: Main
        },
        {
            // 运营管理 - 品类配置
            path: '/catgConfig',
            name: 'catgConfig',
            component: Main
        },{
            // 运营管理 - 品类创建
            path: '/catgAdd',
            name: 'catgAdd',
            component: Main
        },{
            // 运营管理 - 品类编辑
            path: '/catgEdit',
            name: 'catgEdit',
            component: Main
        },
        {
            // 404页面
            path: '/hello',
            name: 'hello',
            component: Hello
        },
        {
            // 404页面
            path: '*',
            name: 'notFound',
            component: NotFoundComponent
        }
    ]
});



