<template>
    <div v-title data-title="页面没有找到">
        <div class="text-center c6 notFound">
            <p><span class="iconfont icon-wangyediushi notFound-icon"></span></p>
            <p>页面被小怪兽吃掉了，找不到了...</p>
        </div>
        <div class="notFound-btn col-xs-10 col-xs-offset-1">
            <button type="button" class="btn btn-primary btn-block" v-on:click="routeTo()">返回查询页面</button>
        </div>
    </div>
</template>

<script>

// import { mapGetters } from 'vuex';
// import * as types from '@/vuex/modules/state-types.js';
import router from '@/router';


export default {
    name: 'notFound',
    data() {
        return {

        };
    },
    methods: {
        // 跳转页面
        routeTo() {
            router.replace({
                path: '/'
            });
        }
    }
};

</script>

<style lang="scss" scoped>
.notFound{margin-top: 100px;}
.notFound-icon{font-size: 200px;}
.notFound-btn{margin-top: 50px;}
</style>




