import router from '@/router';

export default {
    name: 'userCategoryList',
    data() {
        return {
            tableData: [{
                1: '广州拓恪汽车部件有限公司',
                2: 'TATK',
                3: '刹车片',
                4: '2017-07-10 18:43:05',
                5: '2017-07-10 18:43:05'
            }, {
                1: '广州拓恪汽车部件有限公司',
                2: 'TATK',
                3: '刹车片',
                4: '2017-07-10 18:43:05',
                5: '2017-07-10 18:43:05'
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        handleFn(index, rows) {
            if(index == undefined && !rows){
                this.routeTo('userCategoryAdd');
            }else{
                console.log(rows);
                console.log(arguments);
                this.routeTo('userCategoryDetail');
            }

        },
        // 改变每页条数
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        // 分页数据
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        },
        // 跳转页面
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



