import router from '@/router';

export default {
    name: 'userCategoryAdd',
    data() {
        return {
            ruleForm: {
                company: '',
                brand: '',
                category: '',
                part: [],
                price: '',
                dateStart: '',
                dateEnd: '',
                salesman: ''
            },
            rules: {
                company: [
                    { required: true, message: '请输入开通公司' }
                ],
                brand: [
                    { required: true, message: '请选择使用品牌' }
                ],
                category: [
                    { required: true, message: '请选择开通品类' }
                ],
                part: [
                    { type: 'array', required: true, message: '请至少选择一个开通产品' }
                ],
                price: [
                    { required: true, message: '销售价格不能为空'},
                    { type: 'integer', message: '销售价格必须为整数'}
                ],
                dateStart: [
                    { type: 'date', required: true, message: '请选择时间' }
                ],
                dateEnd: [
                    { type: 'date', required: true, message: '请选择时间' }
                ],
                salesman: [
                    { required: true, message: '请填写销售人员' }
                ]
            },
            companyOptions: [{
                value: '0',
                label: '广州拓恪汽车部件有限公司'
            }, {
                value: '1',
                label: '广州市聚友汽车配件有限公司'
            }, {
                value: '2',
                label: '广州市龟将军汽配有限公司'
            }, {
                value: '3',
                label: '广州霍韦奔德汽车零部件有限公司'
            }, {
                value: '4',
                label: '衡水嘉利制动材料有限公司'
            }],
            brandOptions: [{
                value: '0',
                label: 'GEK'
            }, {
                value: '1',
                label: 'YOP'
            }],
            categoryOptions: [{
                value: '0',
                label: '刹车片'
            }, {
                value: '1',
                label: '滤清器'
            }],
            partOptions: [{
                value: '0',
                label: '前刹车片'
            }, {
                value: '1',
                label: '前感应线'
            }, {
                value: '2',
                label: '后刹车片'
            }, {
                value: '3',
                label: '鼓刹车片'
            }, {
                value: '4',
                label: '后感应线'
            }, {
                value: '5',
                label: '手刹车片'
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    alert('submit!');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        // 跳转页面
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



