<template>
    <div v-title data-title="运营管理-品类配置-创建品类">
        <div class="catgAdd p-30">
            <!-- breadcrumb -->
            <el-breadcrumb separator="/">
                <i class="iconfont icon-home fl m-r-10"></i>
                <el-breadcrumb-item :to="{ path: '/catgConfig' }">品类配置</el-breadcrumb-item>
                <el-breadcrumb-item>创建品类</el-breadcrumb-item>
            </el-breadcrumb>
            <!-- form  -->
            <el-row>
                <el-col :span="14" :offset="4">
                    <div class="title m-b-25">创建品类和产品</div>

                    <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">

                        <el-form-item label="品类名称" prop="catgname" required>
                            <el-input type="text" v-model="ruleForm.catgname"></el-input>
                        </el-form-item>

                        <el-form-item
                            v-for="(item, index) in ruleForm.part"
                            :label="'产品名称' + index"
                            :key="item.key"
                            :prop="'part.' + index + '.value'"
                            :rules="{
                                required: true, message: '产品名不能为空', trigger: 'blur'
                            }"
                        >
                            <el-col :span="20">
                                <el-input v-model="item.value"></el-input>
                            </el-col>
                            <el-col :span="4">
                                <el-button class="m-l-10"  icon="delete" @click.prevent="removePart(item)">删除</el-button>
                            </el-col>
                        </el-form-item>

                        <el-form-item>
                            <el-button icon="plus" @click.prevent="addPart">新增产品</el-button>
                        </el-form-item>

                        <el-form-item>
                            <el-button class="input-width" type="primary" @click="submitForm('ruleForm')"> 创建品类 </el-button>
                        </el-form-item>

                    </el-form>

                </el-col>
            </el-row>

        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgAdd';

export default {
    name: 'catgAdd',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.input-width{width: 30%;}
.title{font-weight: bold; font-size: 18px;}
</style>



