<template>
    <div v-title data-title="用户管理-新品类开通">
        <div>
            <div class="text-right">
                <el-button type="primary" size="large" @click="handleFn()">新品类开通</el-button>
            </div>
            <!-- tabla -->
            <el-table :data="tableData" border class="m-t-25">
                <el-table-column prop="1" label="公司名称"></el-table-column>
                <el-table-column prop="2" label="品牌"></el-table-column>
                <el-table-column prop="3" label="开通品类"></el-table-column>
                <el-table-column prop="4" label="开通时间"></el-table-column>
                <el-table-column prop="5" label="到期时间"></el-table-column>
                <el-table-column prop="6" label="操作" width="80">
                    <template scope="scope">
                        <el-button @click="handleFn(scope.$index, tableData)" type="text" size="small">查看详情</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <el-pagination class="m-t-25 text-center" @size-change="handleSizeChange" @current-change="handleCurrentChange" current-page.sync="0" layout="sizes, prev, pager, next" :page-sizes="[20, 50, 100, 200, 400]" :page-size="20" :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
import pageSet from '@/components/main/js/userCategoryList';

export default {
    name: 'userCategoryList',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";

</style>



