<template>
    <div>
        <!-- tabla -->
        <el-table :data="tableData" border class="m-t-25">
            <el-table-column prop="1" label="公司名称"></el-table-column>
            <el-table-column prop="2" label="手机号"></el-table-column>
            <el-table-column prop="3" label="注册用户名"></el-table-column>
            <el-table-column prop="4" label="注册品牌"></el-table-column>
            <el-table-column prop="5" label="起始品类"></el-table-column>
            <el-table-column prop="6" label="提交时间"></el-table-column>
            <el-table-column prop="7" label="操作" width="80">
                <template scope="scope">
                    <el-button @click="handleFn(scope.$index, tableData)" type="text" size="small">审核</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!-- 分页 -->
        <el-pagination class="m-t-25 text-center" @size-change="handleSizeChange" @current-change="handleCurrentChange" current-page.sync="0" layout="sizes, prev, pager, next" :page-sizes="[20, 50, 100, 200, 400]" :page-size="20" :total="1000">
        </el-pagination>
    </div>
</template>

<script>
export default {
    name: 'reviewed',
    data() {
        return {
            tableData: [{
                1: '广州拓恪汽车部件有限公司',
                2: '13570336092',
                3: 'TT',
                4: 'TATK',
                5: '刹车片',
                6: '2017-07-10 18:43:05'
            }, {
                1: '广州拓恪汽车部件有限公司',
                2: '13570336092',
                3: 'TT',
                4: 'TATK',
                5: '刹车片',
                6: '2017-07-10 18:43:05'
            }]
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        // 操作方法
        handleFn(index, rows) {
            this.$confirm('通过当前商户的进驻申请?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'info'
            }).then(() => {
                rows.splice(index, 1);
                this.$notify.info({
                    title: '消息',
                    message: '用户审核成功',
                    duration: 1000
                });
            });
        },
        // 改变每页条数
        handleSizeChange(val) {
            console.log(`每页 ${val} 条`);
        },
        // 分页数据
        handleCurrentChange(val) {
            console.log(`当前页: ${val}`);
        }
    }
};
</script>

<style lang="scss" scoped>
@import "../../../assets/css/config.scss";
</style>



