<template>
    <div v-title data-title="运营管理-品类配置">

            <!-- top -->
            <el-row>
                <el-col :span="18" class="title">
                    品类配置
                </el-col>
                <el-col :span="6">
                    <div class="text-right">
                        <el-button type="primary" size="large" @click="handleFn()">创建品类</el-button>
                    </div>
                </el-col>
            </el-row>
            <!-- tabla -->
            <el-table :data="tableData" border class="m-t-25">
                <el-table-column prop="1" label="品类"></el-table-column>
                <el-table-column prop="2" label="产品"></el-table-column>
                <el-table-column prop="3" label="创建时间"></el-table-column>
                <el-table-column prop="4" label="更新时间"></el-table-column>
                <el-table-column prop="5" label="状态"></el-table-column>
                <el-table-column prop="6" label="操作" width="80">
                    <template scope="scope">
                        <el-button @click="handleFn(scope.$index, tableData)" type="text" size="small">编辑</el-button>
                    </template>
                </el-table-column>
            </el-table>
            <!-- 分页 -->
            <el-pagination class="m-t-25 text-center" @size-change="handleSizeChange" @current-change="handleCurrentChange" current-page.sync="0" layout="sizes, prev, pager, next" :page-sizes="[20, 50, 100, 200, 400]" :page-size="20" :total="1000">
            </el-pagination>

    </div>
</template>

<script>
import pageSet from '@/components/main/js/catgConfig';

export default {
    name: 'catgConfig',
    data: pageSet.data,
    mounted: pageSet.mounted,
    computed: pageSet.computed,
    methods: pageSet.methods
};
</script>

<style lang="scss" scoped>
@import "../../assets/css/config.scss";
.title{font-size: 18px; font-weight: bold; line-height: 40px;}
</style>



