import router from '@/router';

export default {
    name: 'catgAdd',
    data() {
        return {
            ruleForm: {
                catgname: '',
                part: [{
                    value: ''
                }]
            },
            rules: {
                catgname: [
                    { required: true, message: '品类名称不能为空' }
                ]
            }
        };
    },
    mounted() {
        this.$nextTick(() => {

        });
    },
    methods: {
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.routeTo('catgEdit');
                } else {
                    console.log('error submit!!');
                    return false;
                }
            });
        },
        removePart(item) {
            if(this.ruleForm.part.length == 1 ) return;
            var index = this.ruleForm.part.indexOf(item);
            if (index !== -1) {
                this.ruleForm.part.splice(index, 1);
            }
        },
        addPart() {
            this.ruleForm.part.push({
                value: '',
                key: Date.now()
            });
        },

        // 跳转页面
        routeTo(url) {
            router.push({
                name: url
            });
        }
    }

};



