<template>
    <div v-title data-title="页面加载中">

    </div>
</template>

<script>
import router from '@/router';
import * as types from '@/vuex/modules/state-types.js';
export default {
    name: 'loading',
    data() {
        return {

        };
    },
    mounted() {
        this.$nextTick(() => {
            // 启动页面加载状态
            this.$store.dispatch(types.FETCH_PAGE_LOADING, true);
            setTimeout(() => {
                // 启动页面加载状态
                this.$store.dispatch(types.FETCH_PAGE_LOADING, false);
                router.replace({
                    path: 'login'
                });
            }, 500);
        });
    }
};

</script>

<style lang="scss" scoped>

</style>



